
* {
  font-family: Inter, sans-serif;
}

body {
  background-color: #E7F6FF;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.list-reset {
  list-style: none;
  margin: 0;
}

.wrapper {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  width: 1080px;
  margin: 85px auto 0;
}

.container {
  width: 1240px;
}

header {
  border-bottom: 1px solid #f4f4f4;

  img {
    margin-right: 15px;
  }

  h3, p {
    margin: 0;
  }

}

.content {
  h1 {
    margin: 0;
  }
}


.search-block {
  border: 1px solid #F3F3F3;
  border-radius: 10px;
  padding: 10px 14px;
  position: relative;

  input {
    margin-left: 10px;
    border: none;
    font-size: 14px;
    padding-right: 22px;

    &::placeholder {
      color: #C4C4C4;
    }
  }

  .clear-input {
    position: absolute;
    right: 10px;
  }
}


.items {
  flex-grow: 1;
  overflow: auto;
  margin-bottom: 30px;
}

.cardTotalBlock {

  .cartEmpty {

  }

  li {
    margin-bottom: 20px;
  }

  button, {
    width: 100%;
    cursor: pointer;
    background: #9DD558;
    border-radius: 18px;
    border: none;
    color: #FFFFFF;
    font-weight: 600;
    font-size: 16px;
    padding: 18px;
    transition: all 0.2s;

    &:hover {
      background-color: lighten(#9DD558, 5%);
      background-position: right 13px center;
    }

    &:active {
      background-color: darken(#9DD558, 5%);
    }

    &:disabled {
      background-color: #bebebe;

      animation: button-loading 0.8s ease-in-out infinite;
    }
  }
}

.cartItem {
  padding: 20px 30px 20px 20px;
  border: 1px solid #F3F3F3;
  border-radius: 20px;
  overflow: hidden;

  .cartItemImg {
    flex-shrink: 0;
    height: 70px;
    width: 70px;
    background-size: 70px, 70px;
    background-position: 0 -5px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }

  p {
    font-size: 14px;
    margin: 0;
  }

  b {
    font-size: 12px;
  }

  .removeBtn {
    opacity: 0.5;
    transition: opacity 0.2s;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  ;
  }
}

.dashed {
  flex-grow: 1;
  border-bottom: 1px dashed #dfdfdf;
  margin: 0 10px;
  transform: translateY(-4px);
}


.cartEmpty p {
  text-align: center;
}

.greenButton {

  width: 245px;
  cursor: pointer;
  background: #9DD558;
  border-radius: 18px;
  border: none;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 16px;
  padding: 18px;
  transition: all 0.2s;

  img {
    position: relative;
    transform: rotate(180deg) translate(25px, -2px);
    transition: all 0.2s;
  }


  &:hover {
    background-color: lighten(#9DD558, 5%);

    img {
      transform: rotate(180deg) translate(28px, -2px);
    }
  }

  &:active {
    background-color: darken(#9DD558, 5%);
  }


}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

