.card {
  border: 1px solid #f3f3f3;
  padding: 20px;
  width: 210px;
  border-radius: 40px;
  position: relative;
  transition: all .2s;
  margin-right: 20px;
  margin-bottom: 20px;

  span {
    text-transform: uppercase;
    opacity: 0.5;
    font-size: 13px;
  }

  b {
    font-size: 13px;
  }

  h5 {
    font-weight: 400;
    font-size: 14px;
  }

  .favorite {
    cursor: pointer;
    position: absolute;
    top: 12px;
    left: 12px;
  }

  &:hover {
    transform: translateY(-10px);
    border: 1px solid #F8F8F8;
    box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
  }

  .plus {
    cursor: pointer;
  }
}