
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  z-index: 9;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.8s ease-in-out;
}

.drawer {
  display: flex;
  flex-direction: column;

  position: absolute;
  right: 0;
  background-color: #fff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  height: 100%;
  width: 420px;
  padding: 30px;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;

  h2 {

    margin: 0;
  }
}

.overlayVisible {
  visibility: visible;
  opacity: 1;
  .drawer {
    transform: translateX(0);
  }
}